import { Resources } from '../body/state'
import { collectEntities } from '../dynamics'
import { getPosition, isNamedLocation, Location } from '../dynamics/position'
import { distSquared } from '../geometry'
import { State } from '../state'

export function getResourcesAtLocation(state: State, location: Location): Resources {
  return {
    luminocity: getResourceAtLocation(state, 'luminocity', location),
    magnetism: getResourceAtLocation(state, 'magnetism', location),
    radiation: getResourceAtLocation(state, 'radiation', location),
    metals: getResourceAtLocation(state, 'metals', location),
    gases: getResourceAtLocation(state, 'gases', location),
    water: getResourceAtLocation(state, 'water', location),
  }
}

export function getResourceAtLocation(state: State, resource: keyof Resources, location: Location): number {
  switch (resource) {
    case 'metals':
    case 'gases':
    case 'water': {
      return isNamedLocation(location) ? state.bodies.resources[location]?.[resource] || 0 : 0
    }
    case 'luminocity':
    case 'magnetism':
    case 'radiation': {
      const position = getPosition(state, location)
      const entities = collectEntities(state, position.system)
      return entities
        .filter((id) => state.bodies.resources[id]?.[resource] !== undefined)
        .map((id) => {
          const amount = state.bodies.resources[id][resource] || 0
          const resourcePosition = getPosition(state, id)
          const d = distSquared(position.x, position.y, resourcePosition.x, resourcePosition.y)
          return d < 1 ? amount : amount / d
        })
        .reduce((a, b) => a + b, 0)
    }
  }
}
